
import { Options, Vue } from "vue-class-component";

import { ClassDecorator, FunAttri } from "@lowcodeui/relation";
import packageInfo from "../package.json";
import * as fetch from "@/utils/fetch";
import { ElButton } from "element-plus";
import "element-plus/lib/theme-chalk/el-button.css";

@Options({
  name: packageInfo.name,
  components: { ElButton },
  props: {},
  watch: {}
})
@ClassDecorator
export default class Text extends Vue {
  private packageInfo = packageInfo;
  private fetch = fetch;
  @FunAttri({ name: "值", type: "input", defaultvalue: "默认按钮" })
  private value = "默认按钮";
  @FunAttri({
    name: "类型",
    type: "select",
    value: ["primary", "success", "warning", "danger ", "info ", "text"],
    defaultvalue: ""
  })
  private type = "";
  @FunAttri({ name: "图标类名", type: "input", defaultvalue: "" })
  private icon = "";
  @FunAttri({
    name: "尺寸",
    type: "select",
    value: ["medium", "small", "mini"],
    defaultvalue: ""
  })
  private size = "";
  @FunAttri({ name: "是否朴素按钮", type: "bollean", defaultvalue: false })
  private plain = false;
  @FunAttri({ name: "是否圆角按钮", type: "bollean", defaultvalue: false })
  private round = false;
  @FunAttri({ name: "是否圆形按钮", type: "bollean", defaultvalue: false })
  private circle = false;
  @FunAttri({ name: "是否加载中状态", type: "bollean", defaultvalue: false })
  private loading = false;
  @FunAttri({ name: "是否禁用状态", type: "bollean", defaultvalue: false })
  private disabled = false;
  @FunAttri({ name: "是否默认聚焦", type: "bollean", defaultvalue: false })
  private autofocus = false;
  @FunAttri({
    name: "原生 type 属性",
    type: "select",
    value: ["button", "submit", " reset"],
    defaultvalue: "button"
  })
  private nativetype = "button";
  @FunAttri({
    name: "事件",
    type: "textarea",
    defaultvalue: `() => {
    console.log(111);
  }`
  })
  private onClick = () => {
    console.log(111);
  };
}
